// ** React Imports
import { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'

// ** User Edit Components
import SocialTab from './Social'
import AccountTab from './Account'
import InfoTab from './Information'

// ** Store & Actions
import { getUser } from '@store/Users/actions'
import { useDispatch, useSelector } from 'react-redux'

// ** Third Party Components
import { Info, Share2, User } from 'react-feather'
import { Alert, Card, CardBody, Col, Nav, NavItem, NavLink, Row, TabContent, TabPane } from 'reactstrap'

// ** Custom Components
import UILoader from '@components/ui-loader'

// ** Styles
import '@styles/react/apps/app-users.scss'

const UserEdit = () => {
  // ** States & Vars
  const [activeTab, setActiveTab] = useState('1'),
    dispatch = useDispatch(),
    { isLoading, selectedUser } = useSelector(state => state.users),
    { id } = useParams()

  // ** Function to toggle tabs
  const toggle = tab => setActiveTab(tab)

  // ** Function to get user on mount
  useEffect(() => {
    dispatch(getUser(id))
  }, [dispatch])

  return selectedUser !== null && selectedUser !== undefined ? (
    <UILoader blocking={isLoading}>
      <Row className='app-user-edit'>
        <Col sm='12'>
          <Card>
            <CardBody className='pt-2'>
              <Nav pills>
                <NavItem>
                  <NavLink active={activeTab === '1'} onClick={() => toggle('1')}>
                    <User size={14} />
                    <span className='align-middle d-none d-sm-block'>Account</span>
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink active={activeTab === '2'} onClick={() => toggle('2')}>
                    <Info size={14} />
                    <span className='align-middle d-none d-sm-block'>Information</span>
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink active={activeTab === '3'} onClick={() => toggle('3')}>
                    <Share2 size={14} />
                    <span className='align-middle d-none d-sm-block'>Social</span>
                  </NavLink>
                </NavItem>
              </Nav>
              <TabContent activeTab={activeTab}>
                <TabPane tabId='1'>
                  <AccountTab selectedUser={selectedUser} />
                </TabPane>
                <TabPane tabId='2'>
                  <InfoTab />
                </TabPane>
                <TabPane tabId='3'>
                  <SocialTab />
                </TabPane>
              </TabContent>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </UILoader>
  ) : (
    <Alert color='danger'>
      <h4 className='alert-heading'>User not found</h4>
      <div className='alert-body'>
        User with id: {id} doesn't exist. Check list of all Users: <Link to='/users/list'>Users List</Link>
      </div>
    </Alert>
  )
}

export default UserEdit
