// ** React Imports
import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import classnames from "classnames"

// ** Custom Components
import InputGroup from '@components/input-group'
import UILoader from '@components/ui-loader'
import UserEditAvatar from '@components/user-avatar/UserEditAvatar'

// ** Third Party Components
import { Button, Col, Form, Row } from 'reactstrap'

// ** Store Actions
import { updateUser, userEditChange } from '@store/Users/actions'

// ** Utils UploadFile
import uploadFile from '@utils/uploadFile'
import CONSTANTS from "../../../constants"

const UserAccountTab = ({
  selectedUser
}) => {
  // ** States
  const [imgLoading, setImgLoading] = useState(false)

  const dispatch = useDispatch(),
    { formErrors, isLoading } = useSelector(state => state.users),
    { rolesConfig } = useSelector(state => state.roles)

  const handleFormChange = (name, value) => {
    dispatch(userEditChange(name, value))
  }

  // ** Function to change user image
  const onChange = async e => {
    const files = e.target.files
    setImgLoading(true)
    try {
      const url = await uploadFile(files[0])
      handleFormChange('profilePictureURL', url)
      setImgLoading(false)
    } catch (e) {
      setImgLoading(false)
    }
  }

  // ** Function to handle form submit
  const handleSubmit = event => {
    event.preventDefault()
    dispatch(updateUser(selectedUser._id))
  }

  const { membership: membershipConfig, status: statusConfig } = rolesConfig[CONSTANTS.ROLES.USER]
  const membershipOptions = Object.keys(membershipConfig).map((mKey) => {
    return <option key={mKey} value={mKey}>{membershipConfig[mKey]}</option>
  })

  const statusOptions = Object.keys(statusConfig).map((sKey) => {
    return <option key={sKey} value={sKey}>{statusConfig[sKey]}</option>
  })

  return (
    <Row>
      <Col sm='12'>
        <UserEditAvatar
          imgLoading={imgLoading}
          name={`${selectedUser.firstName} ${selectedUser.lastName}`}
          photo={selectedUser.profilePictureURL}
          onChangeAvatar={onChange}
        />
      </Col>
      <Col sm='12'>
        <Form onSubmit={handleSubmit}>
          <Row>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'First Name'}
                error={formErrors['firstName']}
                name={'firstName'}
                placeholder={'First Name'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedUser.firstName}
                required={true}
                className={classnames({ 'is-invalid': formErrors['firstName'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Last Name'}
                error={formErrors['lastName']}
                name={'lastName'}
                placeholder={'Last Name'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedUser.lastName}
                required={true}
                className={classnames({ 'is-invalid': formErrors['lastName'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Username'}
                error={formErrors['userName']}
                name={'userName'}
                placeholder={'Username'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedUser.userName}
                required={true}
                className={classnames({ 'is-invalid': formErrors['userName'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'text'}
                label={'Email'}
                error={formErrors['email']}
                name={'email'}
                placeholder={'Email'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedUser.email}
                required={true}
                className={classnames({ 'is-invalid': formErrors['email'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'select'}
                label={'Country Code'}
                error={formErrors['countryCode']}
                name={'countryCode'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedUser.countryCode}
                required={true}
                className={classnames({ 'is-invalid': formErrors['countryCode'] })}
              >
                <option value=''>Select Country Code</option>
                <option value='254'>+254 Kenya</option>
              </InputGroup>
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'number'}
                label={'Phone Number'}
                error={formErrors['phoneNumber']}
                name={'phoneNumber'}
                placeholder={'Phone Number'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedUser.phoneNumber}
                required={true}
                className={classnames({ 'is-invalid': formErrors['phoneNumber'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'select'}
                label={'Select Plan'}
                error={formErrors['membershipType']}
                name={'membershipType'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedUser.membershipType}
                required={true}
              >
                <option value=''>Select Plan</option>
                {membershipOptions}
              </InputGroup>
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'select'}
                label={'Status'}
                error={formErrors['status']}
                name={'status'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedUser.status}
                className={classnames({ 'is-invalid': formErrors['status'] })}
              >
                <option value=''>Select Status</option>
                {statusOptions}
              </InputGroup>
            </Col>
            <Col className='d-flex flex-sm-row flex-column mt-2' sm='12'>
              <UILoader blocking={isLoading} className='mr-sm-1'>
                <Button.Ripple className='mb-1 mb-sm-0 mr-0' type='submit' color='primary'>
                  Save Changes
                </Button.Ripple>
              </UILoader>
              <Button.Ripple color='secondary' outline>
                Reset
              </Button.Ripple>
            </Col>
          </Row>
        </Form>
      </Col>
    </Row>
  )
}

export default UserAccountTab
